.card__description {
    margin-top: 66px;
    margin-left: auto;
    margin-right: auto;

    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 14px;
    line-height: calc(21 / 14);

    text-align: center;
    letter-spacing: 0.04em;

    color: var(--white-text-color);

    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: calc(25 / 14);
    }

    @media screen and (min-width: 1200px) {
        margin-top: 70px;
        font-size: 16px;
        line-height: calc(28 / 16);
    }
}

.products-details {
    color: var(--white-text-color);
    font-family: 'DM Sans';
    font-size: 12px;
    line-height: calc(18 / 14);
    text-align: center;
    letter-spacing: 0.04em;

    border-radius: 4px;
    padding: .5em .5em 0;
}

.products-details__summary {
    list-style: none;
}

.products-details__summary::before {
    // outline: 1px solid tomato;
    position: relative;
    margin-top: 30px;
    width: 40px;
    height: 40px;
    left: 50%;
    transform: translateX(-50%);
    content: url(../images/products/arrow-right.png);
    display: block;
}

@media (min-device-pixel-ratio: 2),
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    .products-details__summary::before {
        content: url(../images/products/arrow-right-2x.png);
    }
}

.products-details[open] {
    padding: 10px;
}

.products-details[open] .products-details__summary {
    // border-bottom: 2px solid #ffffff;
    margin-bottom: 15px;
}